export var createManifest = function (apps) {
    var manifest = {
        name: "FA",
        short_name: "FA",
        icons: [
            {
                src: "/admin/favicon.ico",
                sizes: "64x64 32x32 24x24 16x16",
                type: "image/x-icon",
            },
            {
                src: "/admin/favicon/android-chrome-192x192.png",
                sizes: "192x192",
                type: "image/png",
            },
            {
                src: "/admin/favicon/android-chrome-512x512.png",
                sizes: "512x512",
                type: "image/png",
            },
        ],
        scope: "/",
        start_url: ".",
        theme_color: "#000000",
        background_color: "#ffffff",
        display: "standalone",
        shortcuts: apps.map(function (app) { return ({
            name: app.appName,
            url: app.url,
        }); }),
    };
    var stringManifest = JSON.stringify(manifest);
    var blob = new Blob([stringManifest], { type: "application/json" });
    var manifestURL = URL.createObjectURL(blob);
    var linkElement = document.getElementById("webmanifest");
    linkElement && linkElement.setAttribute("href", manifestURL);
};
